import { Dropdown, DropdownMenu, DropdownItem } from 'element-ui'
import VDialog from '~/components/common/Dialog'
import AreaPicker from '~/components/form/AreaPicker'
import TopicPicker from '~/components/form/TopicPicker'

export default {
  name: 'PinToolDropdown',
  components: {
    VDialog,
    AreaPicker,
    TopicPicker,
    ElDropdown: Dropdown,
    ElDropdownMenu: DropdownMenu,
    ElDropdownItem: DropdownItem
  },
  props: {
    slug: {
      type: String,
      required: true
    },
    isMine: {
      type: Boolean,
      required: true
    },
    topic: {
      type: String,
      default: ''
    },
    area: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      deleting: false,
      showMoveDialog: false,
      newTopic: this.topic,
      newArea: this.area
    }
  },
  methods: {
    openFeedback() {
      this.$toast.info('开发中')
    },
    deletePin() {
      this.$confirm('删除后不可恢复，确认要删除吗？', '提示')
        .then(() => {
          if (this.deleting) {
            return
          }
          this.deleting = true
          this.$axios
            .$post('v1/pin/delete', {
              slug: this.slug
            })
            .then(() => {
              this.$toast.success('删除成功').then(() => {
                window.location = '/'
              })
            })
            .catch((err) => {
              this.$toast.error(err.message)
              this.deleting = false
            })
        })
        .catch(() => {})
    },
    movePin() {
      this.$axios
        .$post('v1/pin/move', {
          slug: this.slug,
          tags: [this.newTopic, this.newArea],
          topic: this.newTopic,
          area: this.newArea
        })
        .then(() => {
          this.$toast.success('移动成功').then(() => {
            window.location.reload()
          })
        })
        .catch((err) => {
          this.$toast.error(err.message)
        })
    }
  }
}
