import { Dialog } from 'element-ui'

export default {
  name: 'VDialog',
  components: {
    ElDialog: Dialog
  },
  props: {
    value: {
      type: Boolean,
      default: false,
      required: true
    },
    customClass: {
      type: String,
      default: ''
    },
    close: {
      type: Boolean,
      default: true
    },
    width: {
      type: String,
      default: '620px'
    },
    height: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: '提示'
    },
    header: {
      type: Boolean,
      default: true
    },
    footer: {
      type: Boolean,
      default: true
    },
    submitText: {
      type: String,
      default: '确定'
    },
    cancelText: {
      type: String,
      default: '取消'
    },
    scroll: {
      type: Function,
      default: null
    },
    fullscreen: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    noMore: {
      type: Boolean,
      default: false
    },
    clickClose: {
      type: Boolean,
      default: true
    },
    theme: {
      type: String,
      validator: val => ~['success', 'danger'].indexOf(val),
      default: 'success'
    }
  },
  data() {
    return {
      dialogVisible: this.value
    }
  },
  computed: {
    computeDialogHeight() {
      if (this.height) {
        return {
          height: this.height
        }
      }
      if (this.scroll) {
        return {
          height: '600px'
        }
      }
      return {
        height: 'auto'
      }
    }
  },
  mounted() {
    this.$watch('value', (val) => {
      this.dialogVisible = val
      window.__closeImageLazy__ = val
    })
    this.$watch('dialogVisible', (val) => {
      this.$emit('input', val)
      window.__closeImageLazy__ = val
    })
  },
  methods: {
    beforeClose(done) {
      done()
      this.$emit('cancel')
      this.dialogVisible = false
    },
    cancel() {
      this.$emit('cancel')
      this.dialogVisible = false
    },
    submit() {
      this.$emit('submit')
    },
    handleScroll(evt) {
      if (!this.scroll || this.loading || this.noMore) {
        return
      }
      const ul = this.$refs.ul
      if (!ul) {
        return
      }
      const main = evt.currentTarget || evt.target
      if (this.$refs.ul.clientHeight - main.clientHeight - main.scrollTop < 30) {
        this.scroll()
      }
    }
  }
}
