import { Upload } from 'element-ui'
import upload from '~/mixins/upload'

export default {
  name: 'CreateCommentForm',
  components: {
    ElUpload: Upload
  },
  mixins: [upload],
  props: {
    pinSlug: {
      type: String,
      required: true
    },
    commentId: {
      type: [String, Number],
      default: ''
    },
    showAvatar: {
      type: Boolean,
      default: true
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '和谐讨论，共建社区'
    }
  },
  data() {
    return {
      content: '',
      loading: false
    }
  },
  computed: {
    avatar() {
      return this.$store.state.user.avatar || 'default-avatar'
    }
  },
  methods: {
    submit() {
      if (!this.$store.state.isAuth) {
        this.$channel.$emit('sign-in')
        return
      }
      if (this.content.trim().length < 2) {
        this.$toast.error('内容至少要两个字')
        return
      }
      if (this.loading) {
        return
      }
      this.loading = true
      this.$axios
        .$post('v1/comment/create', {
          content: this.content.trim(),
          images: this.uploadImageList.map(_ => _.data),
          pin_slug: this.pinSlug,
          comment_id: this.commentId
        })
        .then((data) => {
          this.$emit('submit', data)
          this.content = ''
          this.resetUploaderStatus()
          this.$toast.success('评论成功')
          this.loading = false
        })
        .catch((err) => {
          this.$toast.error(err.message)
          this.loading = false
        })
    }
  }
}
