import { Pagination } from 'element-ui'
import CreateCommentForm from '~/components/form/CreateCommentForm'
import CommentItem from '~/components/comment/CommentItem'

export default {
  name: 'CommentMain',
  components: {
    CreateCommentForm,
    CommentItem,
    ElPagination: Pagination
  },
  props: {
    slug: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      sort: 'time_asc',
      currentPage: 1
    }
  },
  computed: {
    query() {
      return {
        $axios: this.$axios,
        slug: this.slug,
        sort: this.sort,
        count: 10,
        mode: 'jump'
      }
    },
    sorts() {
      return [
        {
          label: '按热度排序',
          value: 'hottest'
        },
        {
          label: '按时间升序',
          value: 'time_asc'
        },
        {
          label: '按时间降序',
          value: 'time_desc'
        }
      ]
    }
  },
  methods: {
    createTop(data) {
      this.$refs.loader.prepend(data)
    },
    createBottom(data) {
      this.$refs.loader.append(data)
    },
    createInner(data) {
      this.$refs.loader.insertAfter(data)
    },
    handleAgree({ id, result, count }) {
      this.$refs.loader.update({
        id,
        value: result,
        key: 'up_vote_status'
      })
      this.$refs.loader.update({
        id,
        value: count,
        key: 'like_count'
      })
    },
    changeCommentSort(sort) {
      this.sort = sort
      this.currentPage = 1
      this.$nextTick(() => {
        this.$refs.loader.refresh()
      })
    },
    handleDelete(id) {
      this.$refs.loader.delete(id)
    },
    handleJump(page) {
      this.$refs.loader.jump(page)
      this.currentPage = page
    },
    handlePatch({ data }) {
      const { result } = data
      if (!result.length) {
        return
      }
      this.$axios
        .$get('v1/comment/patch', {
          params: {
            comment_ids: result.map(_ => _.id).join(',')
          }
        })
        .then((data) => {
          this.$refs.loader.patch(data)
        })
        .catch(() => {})
    }
  }
}
