import UserAvatar from '~/components/user/UserAvatar'
import UserNickname from '~/components/user/UserNickname'
import JsonContent from '~/components/editor/JsonContent'
import CreateCommentForm from '~/components/form/CreateCommentForm'

export default {
  name: 'CommentItem',
  components: {
    UserAvatar,
    UserNickname,
    JsonContent,
    CreateCommentForm
  },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      showCreate: false,
      deleting: false,
      loading: false
    }
  },
  computed: {
    isMine() {
      return this.$store.getters.isMine(this.item.author.slug)
    },
    isAdmin() {
      return this.$store.getters.isAdmin
    }
  },
  methods: {
    handleCreate(value) {
      this.$emit('create', {
        id: this.item.id,
        value
      })
      this.showCreate = false
    },
    clickAgree() {
      if (!this.$store.state.isAuth) {
        this.$channel.$emit('sign-in')
        return
      }
      if (this.isMine) {
        this.$toast.info('不能给自己点赞')
        return
      }
      if (this.loading) {
        return
      }
      this.loading = true
      this.$axios
        .$post('v1/social/toggle', {
          target_slug: this.item.id.toString(),
          target_type: 'comment',
          action_slug: this.$store.state.user.slug,
          method_type: 'up_vote'
        })
        .then((result) => {
          this.loading = false
          this.$emit('agree', {
            id: this.item.id,
            count: result ? this.item.like_count + 1 : this.item.like_count - 1,
            result
          })
        })
        .catch((err) => {
          this.$toast.error(err.message)
          this.loading = false
        })
    },
    deleteComment() {
      this.$confirm('删除后不可恢复，确认要删除吗？', '提示')
        .then(() => {
          if (this.deleting) {
            return
          }
          this.deleting = true
          this.$axios
            .$post('v1/comment/delete', {
              comment_id: this.item.id
            })
            .then(() => {
              this.$toast.success('删除成功')
              this.$emit('delete', this.item.id)
            })
            .catch((err) => {
              this.$toast.error(err.message)
              this.deleting = false
            })
        })
        .catch(() => {})
    }
  }
}
