import { Cascader } from 'element-ui'

export default {
  name: 'AreaPicker',
  components: {
    ElCascader: Cascader
  },
  props: {
    value: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: '选择作品分区，如约会大作战'
    }
  },
  data() {
    return {
      selected: this.value
    }
  },
  computed: {
    options() {
      return this.$store.state.global.myTags.filter(_ => !~['notebook', 'topic'].indexOf(_.slug))
    },
    myTagsFetched() {
      return this.$store.state.global.myTagsFetched
    }
  },
  watch: {
    value(val) {
      this.selected = val
    },
    selected(val) {
      this.$emit('input', val)
    }
  },
  mounted() {
    if (this.$store.state.isAuth) {
      this.$store.dispatch('global/getMyTags')
    } else {
      this.$channel.$when('user-signed', () => {
        this.$store.dispatch('global/getMyTags')
      })
    }
  }
}
