export default {
  name: 'JsonHeader',
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  render(h) {
    const level = this.item.data.level
    return h('h' + this.item.data.level, {
      class: this.$style[`h${level}`],
      domProps: {
        innerHTML: this.item.data.text
      }
    })
  }
}
