import UserAvatar from '~/components/user/UserAvatar'
import UserNickname from '~/components/user/UserNickname'

export default {
  name: 'ContentAuthor',
  components: {
    UserAvatar,
    UserNickname
  },
  props: {
    user: {
      type: Object,
      required: true
    },
    size: {
      type: Number,
      default: 40
    }
  },
  computed: {
    isMine() {
      return this.$store.getters.isMine(this.user.slug)
    }
  }
}
